* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: Custom;
  src: url("custom-font.4ba26051.ttf");
}

body {
  width: 100%;
  background: tan url("https://t.ly/BJJ7") 0 0 / cover no-repeat;
}

.__productList {
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 2.25rem;
  padding: 1.25rem 1.25rem 4rem;
  display: grid;
}

.__eachProduct {
  background: #f5f5f5;
  border: 1.5px solid #25252a;
  border-radius: 6px;
}

.__eachDetails {
  width: 100%;
  border-radius: 4px;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1.25rem;
  margin-inline: auto;
  padding: 1.75rem 1.25rem 1.75rem 2.25rem;
  display: flex;
  box-shadow: 0 0 10px #0003;
}

.__eachContent {
  width: 100%;
  position: relative;
}

.__eachContent .__eachTitle {
  min-width: 300px;
  text-shadow: 2px 1px 5px #fff;
  font-family: Custom, monospace;
  font-size: 2.25rem;
  position: absolute;
  top: 4rem;
  left: 1rem;
}

.__eachDetails img {
  width: 250px;
  height: 250px;
  border: 1.5px solid #25252a;
  border-radius: 4px;
}

.__eachDetails .skeleton {
  width: 250px;
  height: 250px;
  background-color: #00000026;
  border: 1.5px solid #25252a;
  border-radius: 4px;
}

.__eachPrice {
  color: #dc143c;
  font-family: monospace;
  font-size: 21px;
  font-weight: bold;
}

.__formCase {
  text-align: center;
  padding: 5rem 1rem 2rem;
}

#searchInput {
  background-color: #f5f5f5;
  border: none;
  border-radius: 3px;
  padding: .5rem 1.25rem;
  font-family: monospace;
  font-size: 1.25rem;
  box-shadow: 0 0 5px 2px #00000026;
}

