* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: Custom;
    src: url(./custom-font.ttf);
}

body {
    background: url(https://t.ly/BJJ7);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: tan;
}

.__productList {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 2.25rem;
    padding: 1.25rem 1.25rem 4rem;
}

.__eachProduct {
    background: whitesmoke;
    border-radius: 6px;
    border: 1.5px solid #25252a;
}

.__eachDetails {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 1.25rem;
    padding-bottom: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin-inline: auto;
    padding: 1.75rem 1.25rem 1.75rem 2.25rem;
    border-radius: 4px;
    width: 100%;    
}

.__eachContent {
    position: relative;
    width: 100%;
}

.__eachContent .__eachTitle {
    position: absolute;
    font-size: 2.25rem;
    min-width: 300px;
    top: 4rem;
    left: 1rem;
    text-shadow: 2px 1px 5px #fff;
    font-family: 'Custom', monospace;
}

.__eachDetails img {
    width: 250px;
    border-radius: 4px;
    height: 250px;
    border: 1.5px solid #25252a;
}

.__eachDetails .skeleton {
    width: 250px;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.15);
    height: 250px;
    border: 1.5px solid #25252a;
}

.__eachPrice {
    font-size: 21px;
    font-weight: bold;
    color: crimson;
    font-family: monospace;
}

.__formCase {
    text-align: center;
    padding: 5rem 1rem 2rem;
}

#searchInput {
    padding: 0.5rem 1.25rem;
    font-size: 1.25rem;
    border: none;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.15);
    border-radius: 3px;
    font-family: monospace;
    background-color: whitesmoke;
}